<template>
    <v-container grid-list-xl>
        <v-layout justify-center ma-3>
            <v-icon class="mr-3" color="primary" style="font-size: xx-large">accessibility</v-icon>
            <h2>Mis Tareas (con LocalStorage)</h2>
        </v-layout>
        <v-layout row wrap>
            <v-flex md6>
                <v-card class="mb-3" v-for="(item, index) in tasksList" :key="index">
                    <v-card-text>
                        <v-chip label color="pink" text-color="white">
                            <v-icon left>label</v-icon>
                            {{item.title}}
                        </v-chip>
                        <p>{{item.description}}</p>
                        <v-btn color="warning" @click="editTask(index)">Editar</v-btn>
                        <v-btn class="ml-3" color="error" @click="deleteTask(item.id)">Eliminar</v-btn>
                    </v-card-text>
                </v-card>
            </v-flex>

            <v-flex md6>
                <v-card class="mb-3 pa-3" v-if="formAdd">
                    <v-form @submit.prevent="addTask">
                        <v-text-field label="Título de la tarea" v-model="title"></v-text-field>
                        <v-textarea label="Descripción de la tarea" v-model="description"></v-textarea>
                        <v-btn color="success" type="submit" block>Agregar Tarea</v-btn>
                    </v-form>
                </v-card>

                <v-card class="mb-3 pa-3" v-if="!formAdd">
                    <v-form @submit.prevent="edit">
                        <v-text-field label="Título de la tarea" v-model="title"></v-text-field>
                        <v-textarea label="Descripción de la tarea" v-model="description"></v-textarea>
                        <v-btn color="warning" type="submit" block>Editar Tarea</v-btn>
                    </v-form>
                </v-card>
            </v-flex>
        </v-layout>

        <!-- Snackbar -->
        <v-snackbar v-model="snackbar">
            {{message}}
            <v-btn color="pink" text @click="snackbar = false">Cerrar</v-btn>
        </v-snackbar>

    </v-container>
</template>

<script>
    export default {
        name: 'Tasks',
        data() {
            return {
                tasksList: this.readTasks(),
                title: '',
                description: '',
                message: '',
                snackbar: false,
                formAdd: true,
                indexTask: null
            }
        },
        methods: {
            readTasks() {
                return JSON.parse(localStorage.getItem('tasks')) || [];
            },
            addTask() {
                if (this.title === '' || this.description === '') {
                    this.snackbar = true;
                    this.message = 'Completa todos los campos';
                } else {
                    this.tasksList.push({
                        id: Date.now(),
                        title: this.title,
                        description: this.description
                    });
                    localStorage.setItem('tasks', JSON.stringify(this.tasksList));
                    this.snackbar = true;
                    this.message = 'Tarea agregada';
                }
                this.title = '';
                this.description = '';
            },
            deleteTask(id) {
                this.tasksList = this.tasksList.filter((el) => el.id !== id);
                localStorage.setItem('tasks', JSON.stringify(this.tasksList));
                this.snackbar = true;
                this.message = 'Tarea eliminada';
            },
            editTask(index) {
                this.indexTask = index;
                this.formAdd = false;
                this.title = this.tasksList[index].title;
                this.description = this.tasksList[index].description;
            },
            edit() {
                if (this.title === '' || this.description === '') {
                    this.snackbar = true;
                    this.message = 'Completa todos los campos';
                    return;
                }
                this.tasksList[this.indexTask].title = this.title;
                this.tasksList[this.indexTask].description = this.description;
                localStorage.setItem('tasks', JSON.stringify(this.tasksList));
                this.title = '';
                this.description = '';
                this.formAdd = true;
                this.snackbar = true;
                this.message = 'Tarea actualizada';
            }
        },
    }
</script>